* {
  margin: 0;
  padding: 0;
  font-family: 'poppins', sans-serif
}

body {
  min-height: 100vh;
  display: grid;
  place-items: center;
  background: rgba(10, 10, 10, 0.1)
}

.main {
  position: relative;
  display: flex;
  align-items: center;
  flex-direction: column;
  min-width: 300px;
  padding: 15px 10px;
  box-shadow: 5px 5px 10px 5px rgba(0, 0, 0, 0.18);
  background: rgba(255, 255, 255, 0.05);
  border-radius: 10px
}

.display {
  position: relative;
  width: 80%;
  height: 30px;
  margin-bottom: 10px;
}

.display #result {
  font-size: 2em
}

.buttons {
  margin-top: 20px;
  width: 100%
}

.buttons button {
  height: 50px;
  width: 50px;
  padding: 10px;
  border-radius: 10px;
  border: none;
  outline: none;
  margin: 0 10px;
  cursor: pointer;
  font-size: 1.3em;
  box-shadow: inset 5px 5px 5px rgba(0, 0, 0, 0.2), inset -5px -5px 15px rgba(255, 255, 255, 0.1), 5px 5px 15px rgba(0, 0, 0, 0.3), -5px -5px 15px rgba(255, 255, 255, 0.1)
}

.buttons button:hover {
  background: rgba(0, 0, 0, 0.2)
}

#equalTo {
  width: 125px;
  background: #FF8A65
}

#clear {
  background: #FF8A65
}
.clear {
  background: #FF8A65
}
#history {
  position: absolute;
  bottom: 5px;
  right: 0
}

.dark-mode {
  background-color: #222;
  color: #fff;
}